import { FC } from 'react';
import { styled } from '../../utils';
import { TwBaseProps, TwStyleSystemProps, FormattedColor, TWOnChange } from '../../types';
import {
  Checkbox as MantineCheckbox,
  CheckboxProps as MantineCheckboxProps,
  CheckboxGroupProps as MantineCheckboxGroupProps,
  DefaultProps,
} from '@mantine/core';
import { wrapCheckboxOnChange } from '../../utils/inputElementEvents';
import { PropsFrom } from '../../utility-types';

interface ICheckbox extends FC<CheckboxProps> {
  Group: FC<PropsFrom<typeof MantineCheckbox.Group> & CheckboxGroupProps>;
}

export interface CheckboxGroupProps
  extends TwBaseProps,
    TwStyleSystemProps,
    Omit<MantineCheckboxGroupProps, keyof DefaultProps> {}

export interface CheckboxProps
  extends TwBaseProps,
    TwStyleSystemProps,
    Omit<MantineCheckboxProps, keyof DefaultProps | 'color' | 'onChange'> {
  color?: FormattedColor;
  onChange?: TWOnChange<boolean>;
}

const Root = styled(MantineCheckbox)<MantineCheckboxProps>(() => ({}));

const Checkbox: ICheckbox = (props) => {
  return <Root {...props} onChange={wrapCheckboxOnChange(props.onChange)} />;
};

Checkbox.Group = (props: CheckboxGroupProps) => {
  return <MantineCheckbox.Group {...props} />;
};

export { Checkbox };
